body {
  font-family: Roboto, sans-serif;
  margin: 0;
}

h2 {
  margin: 0;
}

p {
  margin: 0;
}

.product-card {
  max-width: 200px;
  padding: 32px 16px 16px;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #F3F3F3;
  transition: all 0.3s ease-out;

  &__image {
    width: 160px;
    height: 134px;
    margin-bottom: 40px;
    display: block;
    margin-left: auto;
    margin-right: auto;

  }

  &__title {
    margin-bottom: 4px;
    color: $color-for-main-text;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
  }

  &__text-code {
    margin-bottom: 16px;
    color: $color-for-secondary-text;
    font-size: 10px;
    line-height: 14px;
  }

  &__rating {
    display: flex;
    margin-bottom: 24px;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__reviews {
    text-align: right;
    font-size: 10px;
    line-height: 14px;
  }

  &__price-wrapper {
    margin-bottom: 16px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__price-text {
    color: $color-for-secondary-text;
    font-size: 12px;
    line-height: 18px;
  }

  &__price-value {
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__button {
    width: 100%;
    padding: 12px;
    border-radius: 5px;
    background-color: $color-for-button;
    color: $color-for-button-text;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px; /* 114.286% */
    text-transform: uppercase;
    border: 1px solid transparent;

    &:hover {
      color: $color-for-button;
      background-color: $color-for-background;
      border: 1px solid;
    }
  }
}

.product-card:hover {
  transform: scale(1.2);
  cursor: pointer;
}

